* {
  box-sizing: border-box;
}
p {
  margin: 0;
  padding: 0;
}
body {
  font-family: 'Roboto', 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #2a2a2a;
  line-height: 1.5;
  background-color: #e0c9cb;
  padding: 10px 20px 10px 20px;
  margin: 0;
}

#search-box {
  font-size: 1.1rem;
  font-weight: 600;
  line-height: 1;
  font: inherit;
  width: 100%;
  padding: 8px;
  margin-bottom: 16px;
  padding: 1rem 1.6rem;

  margin-top: 20px;
  margin-right: auto;
  border-radius: 1.5rem;

  border: 0 solid #e2e8f0;
  box-shadow: 0 0px 6px #a6afc3b3;
  display: flex;
}

#search-box::placeholder {
  font-weight: 400;
  font-size: small;
  color: #676666;
  align-self: center;
  text-transform: uppercase;
}

#search-box:focus {
  outline: 1px solid #ccc;
}

.country-info {
  margin: 0 auto;
  display: grid;
  gap: 20px;
  grid-template-columns: 100%;

}


.card {
  text-decoration: none;
  margin: 0;
  padding: 20px;
  
  background: linear-gradient(90deg, #ebf2fa 50%, rgba(255, 255, 255, 1) 100%);

  display: flex;
  flex-direction: column;


  border-radius: 10px;
  transition: box-shadow 200ms linear;
}
.card :nth-child(3){
  order: 1;
  margin-bottom: 10px;
}
.card :nth-child(2){
  order: 2;

}

.card:hover {
  box-shadow: inset 0px 0px 10px rgba(186, 126, 126, 0.9),
    5px 5px 15px rgb(186, 126, 126, 0.9);
  cursor: pointer;
}

.card > div > img {
  object-fit: contain;
  width: 100%;
  margin-bottom: 5px;
  height: fit-content;
  align-self: center;
  transition: scale 200ms linear;

  box-shadow: 0.5px 0.5px 0.8px #a6afc3b3, 1.6px 1.7px 2.6px -0.8px #a6afc3b3,
    3.9px 4.3px 6.5px -1.7px #a6afc3b3, 9.5px 10.4px 15.8px -2.5px #a6afc3b3;
}

.card:hover img {
  scale: 1.04;
}
.card > ul {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding-left: 10px;
  padding-right: 10px;
  margin: 0;
  margin-bottom: 10px;
}

.card > ul,
.card > li {
  list-style: none;
  color: #2a2a2a;
  display: flex;
  gap: 10px;

}
.card p {
  color: #4e4e4e;
  text-shadow: 0px 2px 3px rgba(255, 255, 255, 0.5);

  font-weight: 800;
}
.bottom-fr {

  text-decoration: none;
  list-style: none;
  margin: 0;
  padding-left: 0;
  color: #4e4e4e;
}

.bottom-fr > li {
  padding-left: 10px;
  font-size: 18px;
  letter-spacing: 0.03rem;
  margin-bottom: 5px;
}

.country{
  display: inline-block;
  font-size: 20px;
  font-weight: 600;
  width: 100%;
  margin-bottom: 10px;
}

@media (min-width: 600px) {
  #search-box{
    width: 390;
  }
  .country-info {
    grid-template-columns: repeat(2, 1fr);
  }
  .card{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: auto;
    grid-column-gap: 20px;
    grid-row-gap: 5px;

  }
  .card :nth-child(3){
    order: 2;
    margin-bottom: 0;
  }
  .card :nth-child(2){
    order: 1;
 
  }
  .card > div > img{
    grid-area: 1 / 1 / 2 / 2;
    width: 240px;
  }
  
  .card > ul{
    grid-area: 1 / 2 / 2 / 3;
    flex-direction: column;
    justify-content: flex-start;
  }
  .bottom-fr{
    grid-area: 2 / 1 / 3 / 3;
  }
  .country{
    font-size: 26px;
  }
  
}
@media (min-width: 1200px) {
  .country-info {
    grid-template-columns: repeat(3, 1fr);
  }
  .card> div>img{
    width: 240px;
  }

}
