* {
  box-sizing: border-box;
}

p {
  margin: 0;
  padding: 0;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2a2a2a;
  background-color: #e0c9cb;
  margin: 0;
  padding: 10px 20px;
  font-family: Roboto, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

#search-box {
  font-size: 1.1rem;
  font-weight: 600;
  line-height: 1;
  font: inherit;
  width: 100%;
  border: 0 solid #e2e8f0;
  border-radius: 1.5rem;
  margin-top: 20px;
  margin-bottom: 16px;
  margin-right: auto;
  padding: 1rem 1.6rem;
  display: flex;
  box-shadow: 0 0 6px rgba(166, 175, 195, .7);
}

#search-box::-ms-input-placeholder {
  color: #676666;
  text-transform: uppercase;
  align-self: center;
  font-size: small;
  font-weight: 400;
}

#search-box::placeholder {
  color: #676666;
  text-transform: uppercase;
  align-self: center;
  font-size: small;
  font-weight: 400;
}

#search-box:focus {
  outline: 1px solid #ccc;
}

.country-info {
  grid-template-columns: 100%;
  gap: 20px;
  margin: 0 auto;
  display: grid;
}

.card {
  background: linear-gradient(90deg, #ebf2fa 50%, #fff 100%);
  border-radius: 10px;
  flex-direction: column;
  margin: 0;
  padding: 20px;
  text-decoration: none;
  transition: box-shadow .2s linear;
  display: flex;
}

.card :nth-child(3) {
  order: 1;
  margin-bottom: 10px;
}

.card :nth-child(2) {
  order: 2;
}

.card:hover {
  cursor: pointer;
  box-shadow: inset 0 0 10px rgba(186, 126, 126, .9), 5px 5px 15px rgba(186, 126, 126, .9);
}

.card > div > img {
  object-fit: contain;
  width: 100%;
  height: fit-content;
  align-self: center;
  margin-bottom: 5px;
  transition: scale .2s linear;
  box-shadow: .5px .5px .8px rgba(166, 175, 195, .7), 1.6px 1.7px 2.6px -.8px rgba(166, 175, 195, .7), 3.9px 4.3px 6.5px -1.7px rgba(166, 175, 195, .7), 9.5px 10.4px 15.8px -2.5px rgba(166, 175, 195, .7);
}

.card:hover img {
  scale: 1.04;
}

.card > ul {
  flex-direction: row;
  justify-content: space-between;
  margin: 0 0 10px;
  padding-left: 10px;
  padding-right: 10px;
  display: flex;
}

.card > ul, .card > li {
  color: #2a2a2a;
  gap: 10px;
  list-style: none;
  display: flex;
}

.card p {
  color: #4e4e4e;
  text-shadow: 0 2px 3px rgba(255, 255, 255, .5);
  font-weight: 800;
}

.bottom-fr {
  color: #4e4e4e;
  margin: 0;
  padding-left: 0;
  text-decoration: none;
  list-style: none;
}

.bottom-fr > li {
  letter-spacing: .03rem;
  margin-bottom: 5px;
  padding-left: 10px;
  font-size: 18px;
}

.country {
  width: 100%;
  margin-bottom: 10px;
  font-size: 20px;
  font-weight: 600;
  display: inline-block;
}

@media (min-width: 600px) {
  #search-box {
    width: 390px;
  }

  .country-info {
    grid-template-columns: repeat(2, 1fr);
  }

  .card {
    grid-column-gap: 20px;
    grid-row-gap: 5px;
    grid-template-rows: auto;
    grid-template-columns: repeat(2, 1fr);
    display: grid;
  }

  .card :nth-child(3) {
    order: 2;
    margin-bottom: 0;
  }

  .card :nth-child(2) {
    order: 1;
  }

  .card > div > img {
    width: 240px;
    grid-area: 1 / 1 / 2 / 2;
  }

  .card > ul {
    flex-direction: column;
    grid-area: 1 / 2 / 2 / 3;
    justify-content: flex-start;
  }

  .bottom-fr {
    grid-area: 2 / 1 / 3 / 3;
  }

  .country {
    font-size: 26px;
  }
}

@media (min-width: 1200px) {
  .country-info {
    grid-template-columns: repeat(3, 1fr);
  }

  .card > div > img {
    width: 240px;
  }
}

